            mode="aspectFill" 
          />
          <view v-else class="avatar-placeholder">üë§</view>
        </view>
        <view class="profile-info">
          <text class="profile-name">{{ applicationInfo?.nickname || 'Âèã‰º¥Áî®Êà∑' }}</text>
          
          <!-- Á≠âÁ∫ß‰ø°ÊÅØ -->
          <view class="level-info" v-if="currentLevel" @click="goToLevelPage">
