          <text class="balance-label">Ë¥¶Êà∑‰ΩôÈ¢ù</text>
          <text class="balance-amount">¬•0.00</text>
          <view class="balance-actions">
            <text class="action-btn" @click="handleBalanceAction('withdraw')">ÊèêÁé∞</text>
            <text class="action-btn" @click="handleBalanceAction('detail')">ÊòéÁªÜ</text>
          </view>
        </view>
      </view>
      
      <!-- ÂäüËÉΩÂàóË°® -->
